@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');

*{
  margin: 0;
  padding: 0;
}

body{
  background-color: #333;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  position: relative;
  min-height: 800px;
}
.All{
  display: flex;
  flex-direction: column;
}
.header{
  background-color: #222;
  padding: 25px 100px;
  font-weight: 600;
}

main {
  margin: 50px 100px;
  width: 50%;
  float: left;
}
aside{
  width: 20%;
  margin-top: 50px;
  margin-left: 5%;
  float: left;
}
main .user {
  margin-bottom: 50px;
  background-color: #3e8fa0;
  padding: 20px;
}

main .delete-icon {
  float: right;
  color: #134b57;
  cursor: pointer;
  transform: scale(2); 
}
main .edit-icon {
  float: right;
  margin-right: 25px;
  color: #165664;
  cursor: pointer;
  transform: scale(2); 
}

main .delete-icon:hover,
main .edit-icon:hover{
  opacity: 0.5;
}


form input:not([type="checkbox"]),
form textarea, 
form button{
  width: 90%;
  padding: 15px 5%;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  margin-bottom: 10px;
  border: 0;
  background: #222;
  color: #fff;
  outline: none;
  font-size: 15px;
}

form input[type="checkbox"]{
  margin: 10px;
}
form button{
  width: 100%;
  cursor: pointer;
  transition: transform 500ms ease;
}
form button:hover{
  transform: scale(1.1);
}

.user form {
  margin-top: 25px;
}
.user form input,
.user form textarea,
.user form button {
  background: #123b57;
}
.user form button:hover {
  transform: scale(1.01);
}

main .user img{
  margin: 10px 0;
}





.caruselDiv{
  width: 100%;
}
.slick-next{
  right: 10px;
}
.slick-prev{
  left: 10px;
}
.slick-slider {
  padding: 0 50px;
  margin-top: 30px;
}

.imgSlidere{
  width: 250px;
  height: 250px;
  margin-top: 20px;
}
.slick-dots{
  width: 93%;
  bottom: -50px;
}
.slidePiece{
  display: flex !important;
  flex-direction: column;
  align-items: center;
  width: 320px !important;
  height: 350px;
  margin: 15px;
  padding-top: 20px;
  box-sizing: border-box;
  background-color: #222;
  text-align: center;
  transition: all 0.3s;
  cursor: pointer;
}
.slidePiece:hover{
  transform: scale(1.02);
  transition: all 0.1s;
}

.footer{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70px;
  margin-top: 30px;
  padding-top: 20px;
  position: absolute;
  bottom: -200px;
  background-color: #222;
}

.footer a {
  color: white;
  text-decoration: none;
  color: dodgerblue;
  font-size: 14px;
}
.telLink{
  margin-right: 50px;
}
.namCont{
  margin-right: 10px;
  color: white;
}
.dateComp{
  display: flex;
  justify-content: right;
  margin-right: 50px;
  width: 100%;
  margin-top: 15px;
  font-size: 12px;
}















